  /* border: 9px solid red; */
}

.Appreciate_Modal {
  position: absolute;

  background-color: hsl(0, 0%, 0%, 0.8);
  top: 12%;
  width: 100%;
  height: 100vh;
}

/* 모달로 진입시 body의 가로 세로스크롤 모두를 비활성화시킴 */
#body_modal {
  overflow-y: hidden;
  overflow-x: hidden;
}

#body_modal2 {
  overflow-y: auto;
  overflow-x: hidden;
}

.SwiperSlide {
  position: absolute;

  top: 12%;

  /* 센터로 정렬하는 flex */
  display: flex;
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;

  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}

.art_modal {
  width: 100%;
  transform: scale(0.4);
}
