@import url(https://fonts.googleapis.com/css2?family=Cinzel&family=Corinthia:wght@700&family=Nanum+Myeongjo&display=swap);
html {
  height: 100%;
  margin: 0;
  height: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  height: 100%;
  margin: 0;
  height: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* 가로 혹은 세로 스크롤 유무설정 ((body태그에서 설정해도 가능하다))*/
/* 팁!! 루트에서 브라우저의 기본제공하는 스크롤바의 위치를 수정할수있다 */
/* #root {
  position: relative;
  min-height: 100vh;
  width: 100%;

  overflow-y: auto;
  overflow-x: hidden;
} */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: white;

  /* overflow-x: hidden; */
  vertical-align: baseline;
}

/* 가로 세로 스크롤바 유무설정 id= root 외곽 컨테이너태그에서도 설정가능 */
body {
  background-color: black;
  overflow-y: auto;
  overflow-x: hidden;
}

:root {
  --en-font-family: 'Corinthia', cursive;
  --en-message-font-family: 'Cinzel', serif;
  --ko-font-family: 'Nanum Myeongjo', serif;

  /* 폰트가 작아서 별도 사이즈해주기 */
  --en-font-size: 3rem;
}

.App {
  height: 100%;
  min-height: 100%;
}

.kic_swiper {
  height: 250px;
}

.Link {
  text-decoration: none;
}

.L_background {
  font-size: 1.5rem;
  font-family: var(--ko-font-family);
}

.Navigation {
  /* border: 5px solid red; */
  background-color: hsl(0, 0%, 0%, 0.1);
  /* 센터로 정렬하는 flex */
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding-bottom: 2.5px;

  position: fixed;

  width: 100%;
  height: 12%;
  z-index: 999;

  -webkit-backdrop-filter: saturate(70%) blur(10px);

          backdrop-filter: saturate(70%) blur(10px);
}

.Navigation_left {
  position: relative;
  left: -50px;
  /* 일일히 길이를 지정해줘야 콘텐츠 끝트머리가 짤리지않는다 */
  width: 18%;
  font-family: var(--en-font-family);
  font-size: var(--en-font-size);

  text-align: center;
  /* border: 5px solid red; */
}

.Navigation_right {
  position: relative;

  display: flex;
  flex-direction: row;
  /* border: 5px solid red; */
  justify-content: space-between;
  align-items: center;

  width: 40%;
  height: 100%;

  font-size: 1.5rem;
}

.Navigation_right_2 {
  position: relative;
  border: none;
  outline: none;
  box-shadow: none;

  margin-top: 5px;
  width: 50%;
  font-size: 2rem;
  /* border: 5px solid red; */

  left: 15%;
  top: -1px;
}

.Navigation_right_3 {
  font-size: 1.5rem;
  font-family: var(--ko-font-family);
}

.Navigation_right_3:hover,
.L_background:hover {
  transform: scale(1.1);
  transition: 0.7s;
}

.Navigation_center {
  width: 15%;
  text-align: center;
}

.Footer {
  position: absolute;
  bottom: 0px;
  width: 100%;
  height: 100px;

  background-color: hsl(0, 0%, 0%, 0.8); /* fallback for old browsers */

  font-size: 1.2rem;
  font-family: var(--ko-font-family);

  line-height: 30px;
  text-align: center;
}

.Background {
  position: absolute;
  color: rgb(0, 0, 0);
  top: 12%;
  width: 100%;
  height: 6000vh;

  /* border: 5px blue solid; */
}

.Background_Images {
  position: fixed;
  left: 0px;
  width: 100%;

  /* display: none; */
  z-index: -1;
}

.Background_Message {
  position: relative;

  width: 90%;
  top: 45vh;
  left: 50%;
  transform: translate(-50%);

  text-align: center;

  /* border: 5px yellow solid; */
  background-color: hsl(35, 100%, 90%, 0.6);

  font-family: var(--en-message-font-family);
  font-size: 3.5rem;
  color: black;

  letter-spacing: 13px;
}

.Background_Message2 {
  position: relative;

  width: 100%;
  top: 100vh;
  left: 50%;
  transform: translate(-36.6%);

  text-align: center;

  /* border: 5px red solid; */
  background-color: hsl(35, 100%, 90%, 0.5);

  font-family: var(--en-message-font-family);
  font-size: 3.5rem;
  color: black;

  letter-spacing: 13px;
}
.artlist {
  position: absolute;
  width: 100vw;
  height: 85%;
  top: 5%;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

  /* border: 5px red solid; */
}
.art {
  width: 60%;
  transition: 0.5s;
}
.art:hover {
  transform: scale(1.1);
}

.Appreciate {
  /* border: 9px solid red; */
}

.Appreciate_Modal {
  position: absolute;

  background-color: hsl(0, 0%, 0%, 0.8);
  top: 12%;
  width: 100%;
  height: 100vh;
}

/* 모달로 진입시 body의 가로 세로스크롤 모두를 비활성화시킴 */
#body_modal {
  overflow-y: hidden;
  overflow-x: hidden;
}

#body_modal2 {
  overflow-y: auto;
  overflow-x: hidden;
}

.SwiperSlide {
  position: absolute;

  top: 12%;

  /* 센터로 정렬하는 flex */
  display: flex;
  display: -webkit-flex;
  justify-content: center;
  align-items: center;
}

.art_modal {
  width: 100%;
  transform: scale(0.4);
}

