.Background {
  position: absolute;
  color: rgb(0, 0, 0);
  top: 12%;
  width: 100%;
  height: 6000vh;

  /* border: 5px blue solid; */
}

.Background_Images {
  position: fixed;
  left: 0px;
  width: 100%;

  /* display: none; */
  z-index: -1;
}

.Background_Message {
  position: relative;

  width: 90%;
  top: 45vh;
  left: 50%;
  transform: translate(-50%);

  text-align: center;

  /* border: 5px yellow solid; */
  background-color: hsl(35, 100%, 90%, 0.6);

  font-family: var(--en-message-font-family);
  font-size: 3.5rem;
  color: black;

  letter-spacing: 13px;
}

.Background_Message2 {
  position: relative;

  width: 100%;
  top: 100vh;
  left: 50%;
  transform: translate(-36.6%);

  text-align: center;

  /* border: 5px red solid; */
  background-color: hsl(35, 100%, 90%, 0.5);

  font-family: var(--en-message-font-family);
  font-size: 3.5rem;
  color: black;

  letter-spacing: 13px;
}
.artlist {
  position: absolute;
  width: 100vw;
  height: 85%;
  top: 5%;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

  /* border: 5px red solid; */
}
.art {
  width: 60%;
  transition: 0.5s;
}
.art:hover {
  transform: scale(1.1);
}
