.Link {
  text-decoration: none;
}

.L_background {
  font-size: 1.5rem;
  font-family: var(--ko-font-family);
}

.Navigation {
  /* border: 5px solid red; */
  background-color: hsl(0, 0%, 0%, 0.1);
  /* 센터로 정렬하는 flex */
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding-bottom: 2.5px;

  position: fixed;

  width: 100%;
  height: 12%;
  z-index: 999;

  backdrop-filter: saturate(70%) blur(10px);
}

.Navigation_left {
  position: relative;
  left: -50px;
  /* 일일히 길이를 지정해줘야 콘텐츠 끝트머리가 짤리지않는다 */
  width: 18%;
  font-family: var(--en-font-family);
  font-size: var(--en-font-size);

  text-align: center;
  /* border: 5px solid red; */
}

.Navigation_right {
  position: relative;

  display: flex;
  flex-direction: row;
  /* border: 5px solid red; */
  justify-content: space-between;
  align-items: center;

  width: 40%;
  height: 100%;

  font-size: 1.5rem;
}

.Navigation_right_2 {
  position: relative;
  border: none;
  outline: none;
  box-shadow: none;

  margin-top: 5px;
  width: 50%;
  font-size: 2rem;
  /* border: 5px solid red; */

  left: 15%;
  top: -1px;
}

.Navigation_right_3 {
  font-size: 1.5rem;
  font-family: var(--ko-font-family);
}

.Navigation_right_3:hover,
.L_background:hover {
  transform: scale(1.1);
  transition: 0.7s;
}

.Navigation_center {
  width: 15%;
  text-align: center;
}
